import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { CURRENCY_SYMBOL } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../../CommonDataTable'

const MyProperties = () => {
    const userRole = JSON.parse(localStorage.getItem('userData')).role
    const history = useHistory()
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [BranchList, setBranchList] = useState([])
    const [PropertyList, setPropertyList] = useState([])
    const [TableDataList, setTableDataList] = useState([])
    const [selectedBranch, setSelectedBranch] = useState('')
    const [resetData, setReset] = useState(false)

    useEffect(() => {
        useJwt.PropertyList().then(res => {
            const responseData = res.data.payload
            console.log(responseData)
            setSelectedBranch(responseData[0].branch_name)
            setPropertyList(responseData)
            setTableDataList(responseData[0].houses)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
        })
    }, [resetData])
    const handlePoPupActions = (id, message) => {
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                console.log({ id })
                return useJwt.deleteProperty({ id }).then(res => {
                    Success(res)
                    console.log(res)
                    setTableDataList(TableDataList.filter(x => x.id !== id))
                }).catch(err => {
                    console.log(err)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {
            }
        })
    }
    const column = [
        {
            name: 'House Address',
            minWidth: '100px',
            // sortable: true,
            selector: 'house_details'
        },
        {
            name: 'Rent',
            minWidth: '100px',
            sortable: true,
            selector: row => `${CURRENCY_SYMBOL} ${row.property_rent}`
        },
        // {
        //     name: 'Status',
        //     minWidth: '100px',
        //     // sortable: true,
        //     selector: row => {
        //         const statusBG = {
        //             0: { title: 'Blocked', color: 'light-danger' },
        //             1: { title: 'Active', color: 'light-success' }
        //         }
        //         return <Badge color={statusBG[row.status].color} pill className='px-1'>
        //             {statusBG[row.status].title}
        //         </Badge>
        //     }
        // },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Edit">
                        <Edit size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                localStorage.setItem('EditPropertyInfo', JSON.stringify(row))
                                history.push(`/${userRole}/EditProperty`)
                            }}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 'Do you want to delete?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom titleSelect'>
                <CardTitle tag='h4'>My Properties by Branch</CardTitle>
                <CardTitle >
                    <Select
                        // ref={branch_idRef}
                        theme={selectThemeColors}
                        className='react-select'
                        classNamePrefix='select'
                        name='branch_id'
                        maxMenuHeight={150}
                        onChange={selected => {
                            // console.log(PropertyList.filter(p => p.branch_name === selected.value)[0].houses)
                            setSelectedBranch(selected.value)
                            setTableDataList(PropertyList.filter(p => p.branch_name === selected.value)[0].houses)
                        }}
                        options={PropertyList.map(x => { return { value: x.branch_name, label: x.branch_name } })}
                        value={{ value: selectedBranch, label: selectedBranch }}
                        // isDisabled={!ukaddress.length}
                        isClearable={false}
                    />
                </CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={TableDataList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default MyProperties